	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import aboutus from '@/views/aboutus/list'
	import yuyueweixiu from '@/views/yuyueweixiu/list'
	import shebeiweixiu from '@/views/shebeiweixiu/list'
	import wodedingdan from '@/views/wodedingdan/list'
	import shebeixinxi from '@/views/shebeixinxi/list'
    import menu_manage from '@/views/menu_manage/list'
	import fuwupingjia from '@/views/fuwupingjia/list'
	import users from '@/views/users/list'
	import shebeileixing from '@/views/shebeileixing/list'
	import weixiugong from '@/views/weixiugong/list'
	import systemintro from '@/views/systemintro/list'
	import yonghu from '@/views/yonghu/list'
	import gongzijixiao from '@/views/gongzijixiao/list'
	import liuyanfankui from '@/views/liuyanfankui/list'
	import config from '@/views/config/list'
	import usersCenter from '@/views/users/center'
	import weixiugongRegister from '@/views/weixiugong/register'
	import weixiugongCenter from '@/views/weixiugong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/usersCenter',
			name: '',
			component: usersCenter
		}
		,{
			path: '/weixiugongCenter',
			name: '',
			component: weixiugongCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/aboutus',
			name: '',
			component: aboutus
		}
		,{
			path: '/yuyueweixiu',
			name: '',
			component: yuyueweixiu
		}
		,{
			path: '/shebeiweixiu',
			name: '',
			component: shebeiweixiu
		}
		,{
			path: '/wodedingdan',
			name: '',
			component: wodedingdan
		}
		,{
			path: '/shebeixinxi',
			name: '',
			component: shebeixinxi
		}
        ,{
            path: '/menu',
            name: '',
            component: menu_manage
        }
		,{
			path: '/fuwupingjia',
			name: '',
			component: fuwupingjia
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/shebeileixing',
			name: '',
			component: shebeileixing
		}
		,{
			path: '/weixiugong',
			name: '',
			component: weixiugong
		}
		,{
			path: '/systemintro',
			name: '',
			component: systemintro
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/gongzijixiao',
			name: '',
			component: gongzijixiao
		}
		,{
			path: '/liuyanfankui',
			name: '',
			component: liuyanfankui
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		]
	},
	{
		path: '/weixiugongRegister',
		name: '',
		component: weixiugongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
